html {
  font-size: 16px;
  font-family: Helvetica, Arial, sans-serif;
}

h2 {
  font-size: 1rem;
}

body {
margin: 0;

  text-align: center;

  background-color: white;

  -webkit-transition: background-color 0.2s, color 0.2s;

  transition: background-color 0.2s, color 0.2s;

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;
}

/*
.desktop {
  scroll-snap-type: y mandatory;
}
*/

.night {
  color: white;

  background-color: #242f3e;
}

.cb_wrapper {
    display: -webkit-flex;
    display: flex;
    float: left;
    /* fallback for IE */
    width: 8em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 2em;

    margin: 5px;

    border-width: 1px;
    border-style: solid;

    border-radius: 1em;

    cursor: pointer;

    -webkit-transition: background-color 0.5s;

    transition: background-color 0.5s;
}

.mark {
    box-sizing: border-box;
    width: 1.2em;
    height: 1.2em;
    margin: 0.4em;

    border-radius: 0.6em;

    -webkit-transition: opacity 0.5s;

    transition: opacity 0.5s;
}

.cb_label {
    margin: 0.5em 0.5em 0.5em 0;

    color: white;
    font-weight: normal;

    -webkit-transition: color 0.5s;

    transition: color 0.5s;
}

.icon {
    width: 0.7em;
    height: 0.7em;
    margin: 0.2em;
}
.crt_wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: stretch;
            justify-content: stretch;
    -webkit-align-items: center;
            align-items: center;
    float: left;
    box-sizing: border-box;
    width: 100%;
    height: 100vh;
    margin: 0;
    padding: 4vh 4vw;
}

/*
.desktop .crt_wrapper {
    scroll-snap-align: start;
}
*/

.ctr_chart {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    box-sizing: border-box;
    width: 100%;


    -webkit-transform: scaleY(-1);


            transform: scaleY(-1);
}

.crt_controls {
    margin-top: 20px;
}

.chart-line {
    stroke-width: 1.5px;

    -webkit-transition: opacity 0.5s;

    transition: opacity 0.5s;
}

.map-line {
    stroke-width: 1px;

    -webkit-transition: opacity 0.5s;

    transition: opacity 0.5s;
}

.chart-bar {
    -webkit-transition: fill 0.5s;
    transition: fill 0.5s;
}

.map-bar {
    -webkit-transition: fill 0.5s;
    transition: fill 0.5s;
}

.animated {
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.intersection-point {
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;

    stroke-width: 2.5px;
}

.intersection-line {
    opacity: 0.5;

    -webkit-transition: stroke 0.2s;

    transition: stroke 0.2s;

    stroke: #182D3B;
}

.night .intersection-line {
    stroke: #FFFFFF;
}

.map-chart {
    width: 100%;
    height: 100%;

    -webkit-transform: scaleY(-1);

            transform: scaleY(-1);

}

.map-svg-wrapper {

    height: 100%;
    overflow: hidden;

}

.map-container {

    position: relative;

    width: calc(100% - 2 * 20px);
    height: 3em;
    margin-top: 20px;
}

.map-window {
    position: absolute;
    top: -2px;

    box-sizing: border-box;
    height: calc(3em + 4px);

    border-width: 2px;
    border-style: solid none;

    border-color: #C0D1E1;

    cursor: move;

    -webkit-transition: border-color 0.2s;

    transition: border-color 0.2s;
}

.night .map-window {
    border-color: #56626D;
}

.map-overlay {

    opacity: 0.6;

    -webkit-transition: fill 0.2s;

    transition: fill 0.2s;

    fill: #E2EEF9;
}

.night .map-overlay {
    fill: #304259;
}

.map-left-edge {
    border-left-style: solid;
    border-radius: 10px 0 0 10px;

    cursor: w-resize;
}

.map-right-edge {
    border-right-style: solid;
    border-radius: 0 10px 10px 0;

    cursor: e-resize;
}

.map-left-edge, .map-right-edge {
    position: absolute;
    top: -2px;

    box-sizing: border-box;

    width: 20px;
    height: calc(3em + 4px);

    border-width: 1px;
    border-color: #fff;

    color: #888;
    line-height: 52px;

    background-color: #C0D1E1;

    -webkit-transition: background 0.2s, border-color 0.2s;

    transition: background 0.2s, border-color 0.2s;
}

.night .map-left-edge,
.night .map-right-edge {
    border-color: #56626D;

    background: #56626D;
}
.tt_wrapper {
    -webkit-transition: opacity 0.2s, -webkit-transform 0.2s;
    transition: opacity 0.2s, -webkit-transform 0.2s;
    transition: transform 0.2s, opacity 0.2s;
    transition: transform 0.2s, opacity 0.2s, -webkit-transform 0.2s;
}

.tt_rect {
    fill: white;
    stroke: gray;

}

.tt_bold {
    font-weight: bold;
}
.grid_line {
    opacity: 0.1;

    -webkit-transition: stroke 0.2s;

    transition: stroke 0.2s;

    stroke: #182D3B;
}

.night .grid_line {
    stroke: #FFFFFF;
}

.grid_wrapper {
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.grid_labels {
    font-size: 10px;

    -webkit-transition: fill 0.2s;

    transition: fill 0.2s;
}

.grid_labelsDefaultColor {
    fill: #242f3e;
}

.night .grid_labelsDefaultColor {
    opacity: 0.8;

    fill: white;
}
#btn-switch {
    display: inline-block;

    padding-bottom: 30px;

    color: #108be3;

    cursor: pointer;

    scroll-snap-align: end;
}

#footer {
    clear: both;
}
